@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
    font-family: capsule;
    src: url(../../public/fonts/Capsule-Black.ttf);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: capsule, sans-serif;
}

body {
    width: 100%;
    overflow-x: hidden;
}

section {
    min-height: 90vh;
    padding-top: 5em;
}

.input-underline {
    border-bottom: 2px solid #EAEAEA !important;
    border-radius: 0 !important;
}

.input-underline:focus {
    border-bottom: 2px solid #656565 !important;
}

.no-scrollbar::-webkit-scrollbar {
    display: none;
}

.no-scrollbar {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.ant-drawer-content-wrapper {
    height: auto !important;
}